.hs-search-field input {
  width: 100%;
  max-width: 559px;
  border-radius: 140px;
  border: 2px solid #1c1c1c;
  background: #fcfcfc;
  box-shadow: -6px 6px 0px 0px rgba(28, 28, 28, 0.1) inset;
  padding: 22px 37px 22px 30px;
  position: relative;
}

.hs-search-field input::before {
  background-image: url("https://cdn.jsdelivr.net/gh/Dev-DigiFresh/BlogStylesBuild/img/search.svg");
  content: "";
}

.hs-search-field__bar:after {
  display: none;
}

.blog-tags__link {
  border-radius: 70px;
  border: 2px solid #1c1c1c;
  background: #fcfcfc;
  color: #1c1c1c;
  opacity: 0.3;
  &:hover {
    opacity: 1;
    transform: none;
    border: 2px solid #1c1c1c;
    background-color: #fcfcfc;
    color: #1c1c1c;
  }
}
